// eslint-disable-next-line
import React,{useEffect } from 'react';
import { Link } from 'react-router-dom';
import './main.css';
import '../components/reset.css';
import $ from "jquery";
import jquery from 'jquery';
import '../components/reset.css';
import { FaFilm } from 'react-icons/fa';


const Main = () => {
  useEffect( ()=>{
    $(window).on('scroll',function(){
      console.log($(window).scrollTop())
      var 높이 = $(window).scrollTop()
      // scroll이 1 일때 transformX(0px)
      // scroll이 300일때 transformX(-300px)
      var translatemount = (-1) * 높이 + (200)
      $('.sliderow1').css('transform', `translate(${translatemount})`)
      $('.sliderow3').css('transform', `translate(${translatemount})`)
      if (높이 >200 && 높이< 500) {
          $('.sliderow1').addClass('slidedown')
          $('.sliderow3').addClass('slidedown')
          $('.sliderow2').addClass('slidedown2')}
      // }else if ( 높이>501){

      // // }
      var sticky = (299/300)* 높이 + (-595/3)
      $('.sliderow1').css('transform', `translateX(${sticky})`)


      var Opacity = (1/700)*높이 + (-5/7)
      $('.section1-text').css('opacity', Opacity)

      var Scale = (1/2200) * 높이 + (3/22)
      $('.section1-video').css('transform', `scale(${Scale})`)
      
      if (높이 <= 680 ){
          $('.section1-text').css('fontSize','100px')
      } else if ( 높이 > 681){
          $('.section1-text').css('fontSize','150px')
      }

      if (높이 <7090){
          $('.changeimg').attr('src','./img/sticky1.jpg' )
          $('.section2').css('background', '#876750')
          $('.sticky-text').css('color','#3A251D')
      }
      else if ( 높이 >7091 && 높이 < 8550 ){
          $('.changeimg').attr('src','./img/sticky2.jpg' )
          $('.section2').css('background', '#9B7687')
          $('.sticky-text').css('color', '#57485F')
      }
      else if ( 높이 > 8551){
          $('.changeimg').attr('src','./img/sticky3.jpg' )
          $('.section2').css('background', '#160A56')
          $('.sticky-text').css('color', '#1b66a3')
      }
  })
  },[])

  return (
    <div>
    <div className="header" id='1'>
      <div className="nav">
        <div className="logo">
          <p><Link to='/'>Cinema Paradiso</Link><FaFilm/></p> 
        </div>
        <div className="menu">             
          <li><a href>Overview</a></li>
        </div>
        <div className="menu2">
          <li><Link to="/movie">movie</Link></li>
        </div>
      </div>
    </div>
    <div className="content">
      <div className="section0">
        <div className="section0-text">
          <h1>Cinema Paradiso</h1>
          <p>Search for All Movies in Cinema Paradiso</p>
        </div>
        <div className="section0-content">
          <div className="sliderow1 slidecontent">
            <img src="img/slide0.jpg" alt="" />
            <img src="img/slide1.jpg" alt="" />
            <img src="img/slide2.jpg" alt="" />
            <img src="img/slide3.jpg" alt="" />
            <img src="img/slide4.jpg" alt="" />
            <img src="img/slide0.jpg" alt="" />
            <img src="img/slide1.jpg" alt="" />
          </div>
          <div className="sliderow2 slidecontent">
            <img src="img/slide5.jpg" alt="" />
            <img src="img/slide6.jpg" alt="" />
            <img src="img/slide7.jpg" alt="" />
            <img src="img/slide8.jpg" alt="" />
            <img src="img/slide9.jpg" alt="" />
            <img src="img/slide5.jpg" alt="" />
            <img src="img/slide6.jpg" alt="" />
          </div>
          <div className="sliderow3 slidecontent">
            <img src="img/slide10.jpg" alt="" />
            <img src="img/slide11.jpg" alt="" />
            <img src="img/slide12.jpg" alt="" />
            <img src="img/slide13.jpg" alt="" />
            <img src="img/slide14.jpg" alt="" />
            <img src="img/slide10.jpg" alt="" />
            <img src="img/slide11.jpg" alt="" />
          </div>
        </div>
      </div>
      <div className="section1">
        <div className="section1-text container">
          <p>The Cinema <br />you're looking for.<br /> Right out of <br /> here.</p>
        </div>
        <div className="section1-video container">
          <video src="./img/main.mp4" autoPlay muted loop preload="metadata" />
        </div>
      </div>
      <div className="section2-header">
        <div className="section2-text container">
          <p>Turn your living<br /> room into <br />a theater.</p>
        </div>
        <div className="section2-img">
          <img src="./img/section2background.png" alt="" />
        </div>
      </div>
      <div className="section2">
        <div className="sticky-img">
          <img src="./img/sticky1.jpg" alt="" className="changeimg" />
        </div>
        <div style={{clear: 'both'}} />
        <div className="sticky-text">
          <p>The Beguiled is a 2017 American Southern Gothic thriller film written and directed by Sofia Coppola,</p> <p>based on the 1966 novel of the same name by Thomas P. Cullinan.It is the second film adaptation of Cullinan's novel, following Don Siegel's 1971 film of the same name.</p>
        </div>
        <div style={{clear: 'both'}} />
        <div className="sticky-text">
          <p>After a Buddhist boy in the highlands of northern India discovers that he is the reincarnation of a centuries-old Tibetan monk,</p><p>His godfather takes him on a journey to discover his past.
          </p>
        </div>
        <div style={{clear: 'both'}} />
        <div className="sticky-text">
          <p>La La Land is a 2016 American musical romance film written and directed by Damien Chazelle.</p>
          <p>Ryan Gosling and Emma Stone as a struggling jazz pianist and an aspiring actress, respectively,</p>
          <p>who meet and fall in love while pursuing their dreams in Los Angeles.</p>
        </div>
      </div>
    </div>
  </div>
  );
};

export default Main;
